* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: white;
  font-family: "Poppins", sans-serif;
}
.nav--bar {
  background-color: white;
  display: flex;
  justify-content: space-around;
  padding: 20px;
  box-shadow: 0px 3.98256px 7.4564px rgba(0, 0, 0, 0.1);
  border-radius: 0 0 20px 20px;
}
.nav--list {
  list-style: none;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.nav--list li {
  padding: 5px;
  margin-left: 20px;
  cursor: pointer;
}
.nav--list .ct-li:hover {
  text-decoration: underline;
  transform: scale(1.1);
  transition: 0.2s;
  text-decoration-thickness: 3px;
  text-underline-offset: 8px;
  color: rgb(223, 53, 53);
}
.log-in {
  /* border: transparent; */
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  border-radius: 10px 0 10px 0;
  background-color: rgb(223, 53, 53);
  color: white;
}
.nav--list .log-in {
  padding: 5px 25px;
}
.log-in:hover {
  transform: scale(1.2);
  transition: 0.2s;
  border-radius: 0 10px 0 10px;
}
/* Top */
.main--top {
  padding-top: 50px;
  display: flex;
  justify-content: space-around;
  width: 100%;
  flex-wrap: wrap;
  /* border: solid red; */
}
/* .top--div1,
.top--div2,
.top--div3 {
  
} */

.top--image {
  text-align: center;
}
.top--header1,
.top--header2 {
  margin-top: 150px;
  font-size: 50px;
}
.top--header2 {
  color: rgb(223, 53, 53);
}

/* Main */
.main {
  padding: 100px 0 100px 0;
  text-align: center;
}
.main--header {
  font-size: 50px;
  text-align: center;
  margin-top: 150px;
}
.main--header2 {
  font-size: 70px;
  margin-top: 150px;
  color: rgba(55, 61, 66, 0.651);
  text-align: center;
}
.p--div {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-top: 50px;
  /* width: 500px; */
}
.main--paragraph {
  padding: 50px 15px 40px 15px;
  width: 350px;
  font-size: 30px;
  background-color: #e63946;
  color: white;
  border-radius: 20px;
  box-shadow: 0px 8.98256px 7.4564px rgba(0, 0, 0, 0.212);
}

.btn-1,
.btn-2,
.btn-3 {
  margin-top: 20px;
  padding: 5px 18px;
  border: transparent;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  border-radius: 10px 0 10px 0;
  background-color: rgb(255, 255, 255);
  color: rgb(223, 53, 53);
}
.btn-1:hover,
.btn-2:hover,
.btn-3:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(223, 53, 53);
  transform: scale(1.2);
  transition: 0.2s;
  border-radius: 0 10px 0 10px;
}

@media only screen and (max-width: 600px) {
  header nav {
    display: none;
  }

  #hamburger-icon {
    display: block;
  }
}

/* Card */
.card--main {
  display: flex;
  /* border: solid red; */
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}
.card--div--container {
  margin: 50px 0 50px 0;
  /* text-align: center; */
}
.spn--1 {
  font-weight: lighter;
}
.spn--2 {
  font-weight: bold;
}
.card--header {
  text-align: center;
  font-size: 50px;
}

/* footer */
.footer {
  margin-top: 100px;
  background-color: white;
  padding: 20px;
  box-shadow: 3.98256px 0px 7.4564px rgba(0, 0, 0, 0.329);
  border-radius: 20px 20px 0 0;
}

.footer-divs {
  text-align: center;
  margin-top: 40px;
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-evenly;
}
.div-icons {
  margin-top: 80px;
  text-align: center;
  /* display: flex;
  justify-content: space-around; */
  font-size: 30px;
}
.icon--1 {
  cursor: pointer;
}
.fa-twitter,
.fa-instagram,
.fa-linkedin {
  color: rgba(0, 0, 0, 0.212);
}
.fa-twitter:hover {
  color: rgb(0, 174, 255);
}
.fa-instagram:hover {
  color: #e63946;
}
.fa-linkedin:hover {
  color: rgb(0, 174, 255);
}

/* Form */
.form {
  margin-top: 50px;
  text-align: center;
  font-size: 20px;
}
.form-header {
  text-align: center;
}

.input-1 {
  border: solid rgba(0, 0, 0, 0.527);
  border-top: none;
  border-left: none;
  border-right: none;
  width: 40%;
  border-width: 1px;
}
.sub-btn {
  padding: 5px 18px;
  border: transparent;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  border-radius: 10px 0 10px 0;
  background-color: rgb(223, 53, 53);
  color: rgb(255, 255, 255);
}
.sub-btn:hover {
  background-color: rgb(223, 53, 53);
  color: white;
  transform: scale(1.2);
  transition: 0.2s;
  border-radius: 0 10px 0 10px;
}

/* Media Query */
@media only screen and (min-width: 240px) and (max-width: 1024px) {
  .top--image {
    width: 300px;
  }
  .top--header1,
  .top--header2 {
    margin-top: 10px;
    text-align: center;
    font-size: 60px;
  }
  .nav--list li {
    font-size: 11px;
  }

  .main--paragraph {
    padding: 50px 15px 40px 15px;
    width: 300px;
    margin: 50px 15px 40px 15px;
  }

  .main--header2 {
    font-size: 60px;
    margin-top: 150px;
    color: rgba(55, 61, 66, 0.651);
  }
  .card--image {
    width: 200px;
  }
}
